.profile{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;

  height: 100vh;
  width: 100vw;

  background: yellow;
}

#name{
  grid-column: 2;
  grid-row: 1;
}

#age{
  grid-column: 2;
  grid-row: 2;
}

#desc{
  grid-column: 2;
  grid-row: 3;
}




.projects{
  display: grid;
  grid-template-columns: auto auto auto auto;


  height: 100vh;
  width: 100vw;

  background: pink;

}

#proj{
  background: red;
  border: 5px solid blue;
  height: 200px;
  width: 200px;
}

.test{
  grid-row: span 2;
}


.contact{
  display: grid;
  grid-template-rows: auto;


  height: 100vh;
  width: 100vw;

  background: yellow;
}